.app__footer{
    width: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: var(--color-black);
    padding-top: 0;
}
.app__footer-links{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 5rem;
    padding: 0 2rem;
}
.app__footer-links_contact,
.app__footer-links_logo,
.app__footere-links_work{
    flex: 1;
    margin: 1rem;
    text-align: center;
}
.app__footer-links_logo img:first-child{
    width: 210px;
    margin-bottom: 0.75rem;
}
.app__footere-links_work p:nth-child(2n+1)
{
    margin-bottom: 1rem;

}
.app__footer-links_icons{
    margin-top: 0.5rem;

}
.app__footer-links_icons svg{
    color: #fff;
    margin: 0.5rem;
    font-size: 24px;
    cursor: pointer;
}
.app__footer-links_icons svg:hover{
    color:#DCCA87;
}
.app__footer-headtext{
    color: #fff;
    font-family:'Cormorant Upright', serif;
    font-weight: 400;
    letter-spacing: 0.4em;
    text-transform: capitalize;
    font-size: 32px;
    line-height: 41.6px;
    margin-bottom: 1rem;
}
.footer__copywrite{
    margin-top: 3rem;
}

@media screen and (min-width:2000px){
    .app__footer-headtext{
        font-size: 51px;
        line-height: 61px;
    }
}
@media screen and (max-width:1150px){
    .app__footer-links{
        align-items: center;
        flex-direction: column;
        padding: 0;
    }
    .app__footer-links_contact,
.app__footer-links_logo,
.app__footere-links_work{
    margin: 2rem 0;
    width: 100%;
}
}
@media screen and (max-width:650px){
    .app__footer{
        padding: 0 0 2rem 0;
    }
}
@media screen and (max-width:350px){
    .app__footer-links_logo img:first-child{
        width: 80%;
    }
}